import { Injectable } from '@angular/core';
import { HttpClient } from '@angular/common/http';
import { ApiResponse } from '@app/shared/models/api-response.model';

import { Project } from '@app/shared/models/project.model';
import { projectAssignment } from '@app/shared/models/project-assignment.model';

@Injectable({
    providedIn:'root'
})
export class ProjectService
{
    constructor(private http:HttpClient){

    }
    getAllProject(status:number){
        return this.http.get<ApiResponse<Project>>(`api/project/all?status=${status}`);
    }
    getProject(projectDetailsId:string){
        return this.http.get<ApiResponse<Project>>(`api/project/${projectDetailsId}`);
    }
    addProject(project:any){
         
        return this.http.post<ApiResponse<Project>>(`api/project/add`,project);
    }
    updateProject(project:Project){
        return this.http.post<ApiResponse<Project>>('api/project/update',project)
    }
    activateProject(projectDetailsId:number){
        return this.http.put<ApiResponse<Project>>(`api/project/activate/${projectDetailsId}`,{})
    }
    deactivateProject(projectDetailsId:number){
        return this.http.put<ApiResponse<Project>>(`api/project/deactivate/${projectDetailsId}`,{})
    }
    isProjectExists(projectName:string){
        return this.http.get<ApiResponse<Project>>(`api/project/exists?projectName=${projectName}`)
    }

    getProjectAssignMent(employeeDetailsId){
        return this.http.get<ApiResponse<projectAssignment>>(`api/project/assignment/all/${employeeDetailsId}?status=1`)
    }
    updateProjectAssignMent(projectAssignment:projectAssignment){
        return this.http.post<ApiResponse<projectAssignment>>('api/project/assignment/update',projectAssignment)
    }

    saveProjectAssignMent(projectAssignment:projectAssignment){
        return this.http.post<ApiResponse<projectAssignment>>('api/project/assignment/save',projectAssignment)
    }
}