import { Component, OnInit, Input, Output, EventEmitter } from '@angular/core';
import { CompanyService } from '../company.service';
import { Company } from '@app/shared/models/company-module.model';
import { SharedService } from '@app/shared/shared.service';
import { AuthService } from '@app/core/service/auth.service';


@Component({
  selector: 'app-add-company',
  templateUrl: './add-company.component.html',
  styleUrls: ['./add-company.component.css']
})
export class AddCompanyComponent implements OnInit {


  @Input() companyType: string;  
  @Output() onClose = new EventEmitter<any>();
  displayDialog: boolean;
  @Input() company: Company;
  displayCompany2:boolean;
  displayCompany3:boolean;
  displayCompany4:boolean;
  displayCompany5:boolean;
  clientErrorMsg: any;
  public addemployeeDisplay: string = "none";
  errLabel: any;

  constructor(private companyService: CompanyService, private sharedService: SharedService,private authService: AuthService) {
    //this.company = new Company();
    //this.company.organizationDetailsId = this.authService.currentUser.organizationDetailsId;
  }

  ngOnInit(): void {
    if (this.companyType == 'Add') {
      this.company = new Company();    
    }
    this.showDialogToAdd();
    this.company.organizationDetailsId = this.authService.currentUser.organizationDetailsId;
  }
  showDialogToAdd() {    
    this.addemployeeDisplay = "block";
    this.hiddenScroll();
  }

  hiddenScroll() {
    try {
      let bodyElement = document.getElementById('modalbody') as HTMLElement;
      if (bodyElement) {
        bodyElement.classList.add('modal-open');
      }
    }
    catch (ex) {
      this.clientErrorMsg(ex, 'hiddenScroll');
    }
  }


  displayScroll() {
    try {
      let bodyElement = document.getElementById('modalbody') as HTMLElement;
      if (bodyElement) {
        bodyElement.classList.remove('modal-open');
      }
    }
    catch (ex) {
      this.clientErrorMsg(ex, 'displayScroll');
    }
  }

  checkCompanyAlreadyExist() {
    this.companyService.isCompanyExists(this.company.companyName).subscribe(res => {
      if (res.statusCode != 200) {
        this.errLabel = res.message;
        this.company.companyName = "";
      }
    }, error => {
      this.errLabel = error;
    })
  }

  save() {
    if(this.companyType == 'Add'){
      this.companyService.addCompany(this.company).subscribe(res => {
        if (res.statusCode == 200) {
          this.addemployeeDisplay = "none";
          this.sharedService.add({ severity: 'success', summary: 'Success', detail: res.message })
          this.displayScroll();
          this.onClose.emit({ type: 'add' });
        } else {
          this.errLabel = res.message;
        }
      }, error => {
        this.errLabel = error;
      })

    }else{
      this.companyService.updateCompany(this.company).subscribe(res => {
        if (res.statusCode == 200) {
          this.addemployeeDisplay = "none";
          this.sharedService.add({ severity: 'success', summary: 'Success', detail: res.message })
          this.displayScroll();
          this.onClose.emit({ type: 'edit' ,company : this.company});
        } else {
          this.errLabel = res.message;
        }
      }, error => {
        this.errLabel = error;
      })
    }
  }
  closeAddmodal() {
    this.addemployeeDisplay = "none";
    this.displayScroll();
    this.onClose.emit({ type: 'cancel' });
  }
}