import { Component, OnInit, Input, Output, EventEmitter } from '@angular/core';
import { SharedService } from '@app/shared/shared.service';
import { AuthService } from '@app/core/service/auth.service';
import { Project } from '@app/shared/models/project.model';
import { ProjectService } from '../project.service';
import { Company } from '@app/shared/models/company-module.model';


@Component({
  selector: 'app-add-project',
  templateUrl: './add-project.component.html',
  styleUrls: ['./add-project.component.css']
})
export class AddProjectComponent implements OnInit {

  @Input() companyList: any[];
  @Input() projectType: string;  
  @Output() onClose = new EventEmitter<any>();
  displayDialog: boolean;
  @Input() project: Project;
  clientErrorMsg: any;
  public addemployeeDisplay: string = "none";
  errLabel: any;

  constructor(private projectService: ProjectService, private sharedService: SharedService,private authService: AuthService) {
    
  }

  ngOnInit(): void {
    if (this.projectType == 'Add') {
      this.project = new Project();    
    }else{
      if(this.project.startDate){
        let date:string[] = this.project.startDate.split('-');       
        this.project.startDateFormat = new Date(parseInt(date[2]),parseInt(date[0])-1,parseInt(date[1]));
      }
      if(this.project.endDate){
        let date:string[] = this.project.endDate.split('-');       
        this.project.endDateFormat = new Date(parseInt(date[2]),parseInt(date[0])-1,parseInt(date[1]));
      }
    }
    console.log(this.companyList);    
    this.showDialogToAdd();   
    
  }
  showDialogToAdd() {
    //this.department = new Department;
    this.addemployeeDisplay = "block";
    this.hiddenScroll();
  }

  hiddenScroll() {
    try {
      let bodyElement = document.getElementById('modalbody') as HTMLElement;
      if (bodyElement) {
        bodyElement.classList.add('modal-open');
      }
    }
    catch (ex) {
      this.clientErrorMsg(ex, 'hiddenScroll');
    }
  }


  displayScroll() {
    try {
      let bodyElement = document.getElementById('modalbody') as HTMLElement;
      if (bodyElement) {
        bodyElement.classList.remove('modal-open');
      }
    }
    catch (ex) {
      this.clientErrorMsg(ex, 'displayScroll');
    }
  }

  checkProjectAlreadyExist() {
    this.projectService.isProjectExists(this.project.projectName).subscribe(res => {
      if (res.statusCode != 200) {
        this.errLabel = res.message;
        this.project.projectName = "";
      }
    }, error => {
      this.errLabel = error;
    })
  }

  save() {
    if(this.project.startDateFormat){
      let day = this.project.startDateFormat.getDate();
      let month = this.project.startDateFormat.getMonth() + 1; // add 1 because months are indexed from 0
      let year = this.project.startDateFormat.getFullYear();
      this.project.startDate = month+"-"+day+"-"+year;
    }
    if(this.project.endDateFormat){
      let day = this.project.endDateFormat.getDate();
      let month = this.project.endDateFormat.getMonth() + 1; // add 1 because months are indexed from 0
      let year = this.project.endDateFormat.getFullYear();
      this.project.endDate = month+"-"+day+"-"+year;
    }else{
      this.project.endDate = undefined;
    }
    if(this.projectType == 'Add'){
      this.projectService.addProject(this.project).subscribe(res => {
        if (res.statusCode == 200) {
          this.addemployeeDisplay = "none";
          this.sharedService.add({ severity: 'success', summary: 'Success', detail: res.message })
          this.displayScroll();
          this.onClose.emit({ type: 'add' });
        } else {
          this.errLabel = res.message;
        }
      }, error => {
        this.errLabel = error;
      })

    }else{
      this.projectService.updateProject(this.project).subscribe(res => {
        if (res.statusCode == 200) {
          this.addemployeeDisplay = "none";
          this.sharedService.add({ severity: 'success', summary: 'Success', detail: res.message })
          this.displayScroll();
          this.onClose.emit({ type: 'edit' ,project : this.project});
        } else {
          this.errLabel = res.message;
        }
      }, error => {
        this.errLabel = error;
      })
    }
  }
  closeAddmodal() {
    this.addemployeeDisplay = "none";
    this.displayScroll();
    this.onClose.emit({ type: 'cancel' });
  }
}