import { Component, OnInit } from '@angular/core';

import { ConfirmationService } from 'src/app/components/api/confirmationservice';
import { CompanyService } from './company.service';
import { Department } from '@app/shared/models/department.model';
import { SharedService } from '@app/shared/shared.service';
import { Company } from '@app/shared/models/company-module.model';
@Component({
  selector: 'app-company',
  templateUrl: './company.component.html',
  styleUrls: ['./company.component.css']
})
export class CompanyComponent implements OnInit {
  
  cols: any[];
  displayDialog: boolean;
  clientErrorMsg: any;
  public editCompanyDisplay: string = 'none';
  public addCompanyDisplay: string = "none";
  companyType: string;
  company: Company;
  lstCompanies: Company[];
  loading:boolean=false;
  status:number = 1;
  constructor(private confirmationService: ConfirmationService,private sharedService:SharedService ,private companyService:CompanyService) {   
  }

  ngOnInit(): void {

    this.cols = [
      // { field: 'departmentDetailsId', header: 'ID' },
      { field: 'companyName', header: 'Company Name' },
      { field: 'description', header: 'Description' },
      { field: 'eiNumber', header: 'EINumber' },

    ];
    this.getComapnies();
  } 
  handleChange(event){
    if(event.index==0){
      this.status = 1;
      this.getComapnies(1) 
    }
    else if(event.index==1){
      this.status = 0;
      this.getComapnies(0) 
    }
  } 
  getComapnies(status:number=1){
    this.loading=true;
    this.lstCompanies=[];
    this.companyService.getAllCompanies(status).subscribe(res=>{
      this.loading=false;
      this.lstCompanies=[];
      if(res.statusCode==200)
      this.lstCompanies=res.responsePayload as Company[];
    },error=>{
      this.loading=false;
    })
  } 
  showDialogToAdd() {
    this.companyType='Add';    
    //this.hiddenScroll();
  }
  onDeptAddCloseClick(event){   
    if(event.type=='add'){
      this.getComapnies();
    }else if(event.type=='edit'){
      this.getComapnies();      
    }
    this.companyType='';    
  }
  hiddenScroll() {
    try {
      let bodyElement = document.getElementById('modalbody') as HTMLElement;
      if (bodyElement) {
        bodyElement.classList.add('modal-open');
      }
    }
    catch (ex) {
      this.clientErrorMsg(ex, 'hiddenScroll');
    }
  }


  displayScroll() {
    try {
      let bodyElement = document.getElementById('modalbody') as HTMLElement;
      if (bodyElement) {
        bodyElement.classList.remove('modal-open');
      }
    }
    catch (ex) {
      this.clientErrorMsg(ex, 'displayScroll');
    }
  }

  save() {

    this.displayScroll();
  }
  confirm(rowData) {
    this.confirmationService.confirm({
      message: 'Are you sure that you want to Inactivate this data?',
      accept: () => {
        this.delete(rowData);
        //Actual logic to perform a confirmation
      }
    });
  }
  delete(rowData:Company) {
    if(this.status == 1){
      this.companyService.deactivateCompany(rowData.companyDetailsId).subscribe(res=>
        {
          if(res.statusCode==200){
            this.sharedService.add({severity:'success',summary:'Success',detail:res.message})
            this.displayDialog = false;
            this.getComapnies();
          }else{
            this.sharedService.add({severity:'error',summary:'Error',detail:res.message})
          }
        }) 
    }else{
      this.companyService.activateCompany(rowData.companyDetailsId).subscribe(res=>
        {
          if(res.statusCode == 200){
            this.displayDialog = false;
            this.sharedService.add({severity:'success',summary:'Success',detail:res.message})
            this.getComapnies(this.status);
          }else{
            this.sharedService.add({severity:'error',summary:'Error',detail:res.message})
          }
        }) 
    }
  }
  onDelete(rowData) {
    this.company=rowData;
  }
  onRowSelect(data: Company) {
    this.company=data;
    this.companyType='Edit';
    // this.editemployeeDisplay = "block";
    // this.newCar = false;
    // this.car = this.cloneCar(data);
    // this.displayDialog = true;
    this.hiddenScroll();
  }
  modalClose() {
    this.editCompanyDisplay = "none";
    this.displayScroll();
  }

  closeAddmodal() {
    this.addCompanyDisplay = "none";
    this.displayScroll();
  }
  
}
